@import url('https://fonts.googleapis.com/css2?family=Knewave&family=Nunito:wght@400;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  font-family: Nunito, sans-serif;

}

.container-teste {
  width: 100%;
  margin: 0 auto;
  background: #888585;
}

.container-teste {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 15px;

}

.logo img {
  width: 100px;
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 0;
  margin-right: 0;
}

.wrap-teste {
  width: 390px;
  background-color: #ffffff;
  border-radius: 10px;
  overflow: hidden;
  padding: 77px 55px 33px 55px;
  box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.2);
}

.teste-form {
  width: 100%;
}

.teste-form-title {
  font-family: Nunito, sans-serif;
  display: block;
  font-size: 30px;
  color: rgb(75, 75, 75);
  line-height: 1.2;
  text-align: center;
  margin-bottom: 10px;
}

.teste-form-title img {
  width: 155px;
  height: 55px;
  margin-right: 10px;
  position: auto;
}

.wrap-input-teste {
  width: 100%;
  position: relative;
  border-bottom: 2px solid rgb(75, 75, 75);
  margin-bottom: 37px;

}

.input-teste {
  font-size: 15px;
  color: rgb(75, 75, 75);
  line-height: 1.2;
  border: none;
  display: block;
  width: 100%;
  height: 45px;
  background-color: transparent;
  padding: 0 5px;
  font-family: Nunito, sans-serif;
  margin-top: -15px;
  

}

.focus-input-teste {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  color: rgb(75, 75, 75);
  font-family: Nunito, sans-serif;
  text-align: left;
}

.focus-input-teste::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
  background: #de940a;
  background: -webkit-linear-gradient(to left, #de940a, #ff5c21);
  background: -o-linear-gradient(to left, #de940a, #ff5c21);
  background: -moz-linear-gradient(to left, #de940a, #ff5c21);
  background: linear-gradient(to left, #de940a, #ff5c21);
}

.focus-input-teste::after {
  font-family: Nunito, sans-serif;
  font-size: 15px;
  color: rgb(56, 56, 56);
  line-height: 1.2;
  content: attr(data-placeholder);
  display: block;
  width: 100%;
  position: absolute;
  top: 16px;
  left: 0px;
  padding-left: 5px;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}

.input-teste:focus {
  outline: 0;
}

.input-teste:focus+.focus-input-teste::after {
  top: -15px;
}

.input:focus+.focus-input-teste::before {
  width: 100%;
}

.has-val+.focus-input-teste::after {
  top: -15px;
}

.has-val+.focus-input-teste::before {
  width: 100%;
}

.container-form-btn-teste {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 13px;
}

.teste-form-btn {
  font-size: 15px;
  border: none;
  border-radius: 10px;
  color: #ffffff;
  line-height: 1.2;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  background: #de940a;
  background: -webkit-linear-gradient(to left, #de940a, #ff5c21);
  background: -o-linear-gradient(to left, #de940a, #ff5c21);
  background: -moz-linear-gradient(to left, #de940a, #ff5c21);
  background: linear-gradient(to left, #de940a, #ff5c21);
}

.teste-form-btn:hover {
  cursor: pointer;
}

.text-center-teste {
